___CSS_LOADER_EXPORT___.push([module.id, "\n.add-owner-button {\n    background: #2C5A86;\n    border-radius: 4px;\n    border: none;\n    padding: 8px 16px;\n    font-size: 14px;\n    font-weight: 500;\n    color: #FFFFFF;\n    display: flex;\n    flex-direction: row;\n    align-items: center;\n    height: 32px;\n    margin-right: 10px;\n}", "",{"version":3,"sources":["webpack://./src/presentation/vue3/pages/backoffice/organizationGroup.vue"],"names":[],"mappings":";AAgVA;IACI,mBAAmB;IACnB,kBAAkB;IAClB,YAAY;IACZ,iBAAiB;IACjB,eAAe;IACf,gBAAgB;IAChB,cAAc;IACd,aAAa;IACb,mBAAmB;IACnB,mBAAmB;IACnB,YAAY;IACZ,kBAAkB;AACtB","sourcesContent":["\n<script setup lang=\"ts\">\nimport { defineAsyncComponent, onBeforeMount, ref } from \"vue\";\nimport { IOrganization } from \"../../../../../../common/qCloudTemp/membership\";\nimport { IGetOrganizaionGroupDetailsResponse } from \"../../../../../../common/qCloudTemp/symDtoObjects\";\nimport { container } from \"../../../../domain/core/diContainer\";\nimport { IApplicationExtended } from \"../../../../domain/model/models\";\nimport { IDialog } from \"../../../../domain/presentation/IDialog\";\nimport { IRouter } from \"../../../../domain/presentation/IRouter\";\nimport { IBackofficeUseCase } from \"../../../../domain/useCase/backoffice/IBackofficeUseCase\";\nimport { IUseCaseExecutor } from \"../../../../domain/useCase/IUseCaseExecutor\";\nimport AppList from \"../../components/backoffice/application/applicationList.vue\";\nimport UserList from \"../../components/backoffice/user/userList.vue\";\n\ninterface IOrganizationData extends IOrganization {\n    showOrgInfo: boolean;\n}\n\nconst router = container.resolve<IRouter>(IRouter);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst backofficeUseCase = container.resolve<IBackofficeUseCase>(IBackofficeUseCase);\nconst dialog = container.resolve<IDialog>(IDialog);\n\nconst routerParams = router.getRouteParams<{ organizationGroupID: string }>();\nbackofficeUseCase.setBreadcrumb([\"Organization Groups\", \"Organization Group Detail\"]);\nconst orgGroup = ref<IGetOrganizaionGroupDetailsResponse>();\nconst allAppsOfOrgGroup = ref<Array<IApplicationExtended>>([]);\nconst showTotalOrgGroup = ref(true);\nconst showByOrganization = ref(false);\nconst showOwners = ref(false);\nconst organizations = ref<Array<IOrganizationData>>();\n\nonBeforeMount(() => {\n    getOrganizationGroupDetails();\n})\nfunction getOrganizationGroupDetails() {\n    executor.execute(async () => {\n        orgGroup.value = await backofficeUseCase.getOrganizationGroupDetails({ organizationGroupId: routerParams.organizationGroupID });\n        allAppsOfOrgGroup.value = backofficeUseCase.getAllAppsForOrgGroup(orgGroup.value!);\n        organizations.value = orgGroup.value.organizations.map((org) => {\n            const organizationData: IOrganizationData = { ...org, showOrgInfo: false };\n            return organizationData;\n        });\n    }, { loading: true });\n}\n\nfunction openUpdateOrgGroupDialog() {\n    const closeCb = (reason: \"close\" | \"outclick\" | \"component\") => {\n        if (reason == \"component\") {\n            getOrganizationGroupDetails();\n        }\n    }\n    dialog.showDialog(\n        defineAsyncComponent(() => import(`../../components/dialogs/backoffice/addOrUpdateOrgGroup.vue`)),\n        { closable: true, title: \"Update Organization Group\", width: \"542px\", closeCb }, { orgGroup: orgGroup.value?.organizationGroup }\n    );\n}\nfunction getAppsByOrganizationId(orgId: string) {\n    const orgApps = backofficeUseCase.getOrganizationAppsByOrganizationIdForOrgGroup(orgId, orgGroup.value!);\n    return orgApps!;\n}\nfunction getUserPerOrganization(orgId: string) {\n    return backofficeUseCase.getUsersPerOrganizationForOrgGroup(orgId, orgGroup.value!);\n}\n\nfunction viewOrganization(orgID: string) {\n    window.open(`/backoffice/organization/${orgID}`, \"_blank\");\n}\nfunction selectTab(tabName: string) {\n    switch (tabName) {\n        case 'total':\n            showTotalOrgGroup.value = true;\n            showByOrganization.value = false;\n            showOwners.value = false;\n            break;\n        case 'byOrganization':\n            showTotalOrgGroup.value = false;\n            showByOrganization.value = true;\n            showOwners.value = false;\n            break;\n        case 'owners':\n            showTotalOrgGroup.value = false;\n            showByOrganization.value = false;\n            showOwners.value = true;\n            break;\n    }\n\n}\nfunction openAddOwnerDialog() {\n    const closeCb = (reason: \"close\" | \"outclick\" | \"component\") => {\n        if (reason == \"component\") {\n            getOrganizationGroupDetails();\n        }\n    }\n    dialog.showDialog(\n        defineAsyncComponent(() => import(`../../components/dialogs/organization/invitePeopleDialog.vue`)),\n        { closable: true, title: \"Add Owner To \" + orgGroup.value?.organizationGroup.name, width: \"658px\", maxHeight: \"700px\", closeCb },\n        { inviteCb: backofficeUseCase.updateOrganizationGroupOwners.bind(backofficeUseCase), ID: orgGroup.value?.organizationGroup.ID, action: 'add', onBackoffice: true }\n    );\n}\n</script>\n<template>\n    <div>\n        <div class=\"update-org-group-div\">\n            <button class=\"update-btn\" @click=\"openUpdateOrgGroupDialog()\"><i class=\"mdi mdi-cog\"\n                    style=\"font-size: 20px; margin-right: 4px\"></i><span>UPDATE ORGANIZATION GROUP</span></button>\n        </div>\n            <div style=\"display: flex; margin-bottom: 32px;\">\n                <div class=\"org-group-info-type\">\n                    <div class=\"org-group-info-type-name\">Name</div>\n                    <div class=\"org-group-info\">{{ orgGroup?.organizationGroup.name }}</div>\n                </div>\n                <div class=\"org-group-info-type\">\n                    <div class=\"org-group-info-type-name\">Description</div>\n                    <div class=\"org-group-info\">{{ orgGroup?.organizationGroup.description }}</div>\n                </div>\n                <div class=\"org-group-info-type\">\n                    <div class=\"org-group-info-type-name\">Total User Count</div>\n                    <div class=\"org-group-info\">{{ orgGroup?.users.length }}</div>\n                </div>\n                <div class=\"org-group-info-type\">\n                    <div class=\"org-group-info-type-name\">Total App Count</div>\n                    <div class=\"org-group-info\">{{ allAppsOfOrgGroup.length }}</div>\n                </div>\n                <div class=\"org-group-info-type\">\n                    <div class=\"org-group-info-type-name\">Organization Count</div>\n                    <div class=\"org-group-info\">{{ orgGroup?.organizations.length }}</div>\n                </div>\n            </div>\n            <div class=\"org-group-org-info\">\n                <div class=\"org-group-tabs\">\n                    <div :class=\"showTotalOrgGroup ? 'active-category' : ''\" @click=\"selectTab('total')\">Total</div>\n                    <div :class=\"showByOrganization ? 'active-category' : ''\" @click=\"selectTab('byOrganization')\">\n                        Organizations\n                    </div>\n                    <div :class=\"showOwners ? 'active-category' : ''\" @click=\"selectTab('owners')\">Owners</div>\n                </div>\n                <div v-if=\"showTotalOrgGroup\" style=\"padding: 30px 24px\">\n                    <UserList v-if=\"orgGroup && orgGroup.users.length > 0\" :users=\"orgGroup.users\"\n                        @refreshTable=\"getOrganizationGroupDetails\"></UserList>\n                    <div v-else>There is no user in this organization group</div>\n                    <AppList v-if=\"orgGroup && allAppsOfOrgGroup.length > 0\" :apps=\"allAppsOfOrgGroup\"\n                        :displayOrgName=\"true\">\n                    </AppList>\n                    <div v-else>There is no application in this organization group</div>\n                </div>\n                <div v-if=\"showByOrganization\" style=\"padding-bottom: 30px\">\n                    <div v-if=\"organizations && organizations.length > 0\">\n                        <div v-for=\"organization in organizations\" :key=\"organization.id\" class=\"org-div\">\n                            <div class=\"org-name\"\n                                :style=\"{ marginBottom: organization.showOrgInfo ? '20px' : '0px', borderBottom: organization.showOrgInfo ? '1px solid black' : 'none', paddingBottom: organization.showOrgInfo ? '10px' : '0' }\">\n                                <span><i class=\"mdi mdi-chart-pie\" style=\"color: #E65100; margin-right: 10px\"></i>{{\n                                    organization.name }}</span>\n                                <div style=\"cursor: pointer; margin-left: auto; display: flex\">\n                                    <button class=\"view-btn\" @click=\"viewOrganization(organization.id)\">VIEW</button>\n                                    <i v-if=\"!organization.showOrgInfo\" class=\"mdi mdi-chevron-down\"\n                                        @click=\"organization.showOrgInfo = !organization.showOrgInfo\"></i>\n                                    <i v-else class=\"mdi mdi-chevron-up\"\n                                        @click=\"organization.showOrgInfo = !organization.showOrgInfo\"></i>\n                                </div>\n                            </div>\n                            <div v-if=\"organization.showOrgInfo\">\n                                <UserList :users=\"getUserPerOrganization(organization.id)\"\n                                    @refreshTable=\"getUserPerOrganization(organization.id)\"></UserList>\n                                <AppList :apps=\"getAppsByOrganizationId(organization.id)\"></AppList>\n                            </div>\n                        </div>\n                    </div>\n                    <div v-else>There is no organization in this organization group</div>\n                </div>\n                <div v-if=\"showOwners\" style=\"padding: 30px 24px\">\n                    <UserList v-if=\"orgGroup\" :users=\"orgGroup.owners\" @refreshTable=\"getOrganizationGroupDetails\"\n                        :orgGroupID=orgGroup.organizationGroup.ID\n                        :addButton=\"{ buttonText: 'ADD OWNER', buttonClass: 'add-owner-button', buttonCb: async () => { openAddOwnerDialog() } }\">\n                    </UserList>\n                </div>\n            </div>\n    </div>\n</template>\n<style scoped>\n.update-org-group-div {\n    width: 85%;\n    display: flex;\n    justify-content: end;\n    position: absolute;\n    top: 80px;\n    right: 36px;\n}\n\n.update-btn {\n    border: 1px solid #151233;\n    border-radius: 4px;\n    padding: 1.5px 16px;\n    font-size: 15px;\n    color: #151233;\n    display: flex;\n    align-items: center;\n    font-weight: 500;\n}\n\n.org-group-info-type {\n    border: 1px solid #E3E3E3;\n    box-shadow: 1px 1px 2px rgba(33, 33, 33, 0.16);\n    border-radius: 10px;\n    background: #FFFFFF;\n    margin-right: 37px;\n    padding: 24px 24px 18px;\n    display: flex;\n    flex-direction: column;\n    flex: 1;\n}\n\n.org-group-info-type:last-child {\n    margin-right: 0;\n}\n\n.org-group-info-type-name {\n    font-weight: 500;\n    font-size: 20px;\n    line-height: 23px;\n    color: #757575;\n    margin-bottom: 20px;\n    min-width: 100px;\n    flex: 1;\n}\n\n.org-group-info {\n    font-weight: 500;\n    font-size: 28px;\n    line-height: 33px;\n    color: #151233;\n}\n\n.org-name {\n    font-size: 30px;\n    line-height: 32px;\n    color: #151233;\n    display: flex;\n\n}\n\n.user-app-header {\n    font-weight: 500;\n    font-size: 24px;\n    line-height: 28px;\n    color: #151233;\n    margin-bottom: 20px;\n}\n\n.user-app-list {\n    background: #FFFFFF;\n    border: 1px solid #E3E3E3;\n    box-shadow: 1px 1px 2px rgba(33, 33, 33, 0.16);\n    border-radius: 10px;\n    width: 100%;\n}\n\ntd:hover {\n    background-color: none !important;\n}\n\n.user-app-list tr:hover:not(:last-child):not(:first-child) {\n    background-color: #EEEEEE;\n}\n\n.user-app-list th {\n    border-bottom: 1px solid #E3E3E3;\n    padding: 18px 36px\n}\n\n.user-app-list td {\n    border-bottom: 1px solid #E3E3E3;\n    padding: 20px 36px;\n    color: #212121;\n    font-size: 14px;\n    line-height: 16px;\n}\n\n.user-app-list .thead {\n    background: #F5F5F5;\n    color: #616161;\n    font-weight: 500;\n    font-size: 14px;\n    line-height: 16px;\n}\n\n.org-group-tabs {\n    display: flex;\n    align-items: center;\n    width: 100%;\n    margin-bottom: 20px;\n    padding: 24px 32px;\n}\n\n.org-group-tabs div {\n    color: #A1A1A1;\n    font-weight: 500;\n    font-size: 14px;\n    width: 111px;\n    padding-bottom: 8px;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    cursor: pointer;\n    border: 2px solid transparent;\n}\n\n.active-category {\n    color: #151233 !important;\n    border-bottom: 2px solid !important\n}\n\n.org-group-org-info {\n    background: #FFFFFF;\n    border-radius: 8px;\n}\n\n.org-div {\n    border-bottom: 1px solid #0000002F;\n    padding: 24px;\n}\n\n.org-div:last-child {\n    border-bottom: none;\n}\n\n.view-btn {\n    background: none;\n    border: none;\n    color: #2C5A86;\n    font-weight: 500;\n    font-size: 14px;\n    line-height: 16px;\n}\n</style>\n<style>\n.add-owner-button {\n    background: #2C5A86;\n    border-radius: 4px;\n    border: none;\n    padding: 8px 16px;\n    font-size: 14px;\n    font-weight: 500;\n    color: #FFFFFF;\n    display: flex;\n    flex-direction: row;\n    align-items: center;\n    height: 32px;\n    margin-right: 10px;\n}</style>"],"sourceRoot":""}]);
